
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"80%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator=">" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="fangwumingcheng">
						<el-input class="list_inp" v-model="form.fangwumingcheng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.fangwumingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="fangwutupian">
						<uploads
							:disabled="!isAdd||disabledForm.fangwutupian?true:false"
							action="file/upload" 
							tip="" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.fangwutupian?form.fangwutupian:''" 
							@change="fangwutupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="" prop="fangwumianji">
						<el-input class="list_inp" v-model="form.fangwumianji" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.fangwumianji?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="fangwudizhi">
						<el-input class="list_inp" v-model="form.fangwudizhi" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.fangwudizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="zulinjiage">
						<el-input class="list_inp" v-model.number="form.zulinjiage" placeholder=""
							 type="number" 							:readonly="!isAdd||disabledForm.zulinjiage?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="fangdongzhanghao">
						<el-input class="list_inp" v-model="form.fangdongzhanghao" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.fangdongzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="fangdongxingming">
						<el-input class="list_inp" v-model="form.fangdongxingming" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.fangdongxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="fangwuzhuangtai">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.fangwuzhuangtai?true:false"
							v-model="form.fangwuzhuangtai" 
							placeholder=""
							style="width:100%;"
							>
							<el-option v-for="(item,index) in fangwuzhuangtaiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="" prop="fulladdress">
						<el-input class="list_inp" v-model="form.fulladdress" placeholder="" readonly @click.native="mapClick" />
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="fangwujieshao">
						<editor class="list_editor" :value="form.fangwujieshao" placeholder="" :readonly="!isAdd||disabledForm.fangwujieshao?true:false"
							@change="(e)=>editorChange(e,'fangwujieshao')"></editor>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick"></el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					
				</el-button>
			</div>
		</el-form>
		<locationForm ref="location" :toParentsMap="form" @mapData="getAddress"></locationForm>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//
	const tableName = 'fangwuxinxi'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	import locationForm from '@/components/location.vue'
	
	const location = ref(null)
	
	const mapClick = () => {
		if(!isAdd.value||disabledForm.fulladdress){
			return false
		}
		location.value.mapShow()
	}
	
	const getAddress = (e) => {
		form.value.lng = e.lng
		form.value.lat = e.lat
		form.value.fulladdress = e.fulladdress
	}
	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form
	const form = ref({
		fangwumingcheng: '',
		fangwutupian: '',
		fangwumianji: '',
		fangwudizhi: '',
		zulinjiage: '',
		fangwujieshao: '',
		storeupnum: '0',
		fangdongzhanghao: '',
		fangdongxingming: '',
		fangwuzhuangtai: '',
		longitude: '',
		latitude: '',
		fulladdress: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		fangwumingcheng : false,
		fangwutupian : false,
		fangwumianji : false,
		fangwudizhi : false,
		zulinjiage : false,
		fangwujieshao : false,
		storeupnum : false,
		fangdongzhanghao : false,
		fangdongxingming : false,
		fangwuzhuangtai : false,
		longitude : false,
		latitude : false,
		fulladdress : false,
	})
	const isAdd = ref(false)
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		fangwumingcheng: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		fangwutupian: [
		],
		fangwumianji: [
		],
		fangwudizhi: [
		],
		zulinjiage: [
			{required: true,message: '',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		fangwujieshao: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		fangdongzhanghao: [
		],
		fangdongxingming: [
		],
		fangwuzhuangtai: [
		],
		longitude: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		latitude: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		fulladdress: [
		],
	})
	//
	const fangwutupianUploadSuccess=(e)=>{
		form.value.fangwutupian = e
	}
	//
	const fangwuzhuangtaiLists = ref([])
	//methods

	//methods
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.fangwujieshao = res.data.data.fangwujieshao.replace(reg,'../../../cl13824148/file');
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='fangwumingcheng'){
					form.value.fangwumingcheng = row[x];
					disabledForm.value.fangwumingcheng = true;
					continue;
				}
				if(x=='fangwutupian'){
					form.value.fangwutupian = row[x];
					disabledForm.value.fangwutupian = true;
					continue;
				}
				if(x=='fangwumianji'){
					form.value.fangwumianji = row[x];
					disabledForm.value.fangwumianji = true;
					continue;
				}
				if(x=='fangwudizhi'){
					form.value.fangwudizhi = row[x];
					disabledForm.value.fangwudizhi = true;
					continue;
				}
				if(x=='zulinjiage'){
					form.value.zulinjiage = row[x];
					disabledForm.value.zulinjiage = true;
					continue;
				}
				if(x=='fangwujieshao'){
					form.value.fangwujieshao = row[x];
					disabledForm.value.fangwujieshao = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='fangdongzhanghao'){
					form.value.fangdongzhanghao = row[x];
					disabledForm.value.fangdongzhanghao = true;
					continue;
				}
				if(x=='fangdongxingming'){
					form.value.fangdongxingming = row[x];
					disabledForm.value.fangdongxingming = true;
					continue;
				}
				if(x=='fangwuzhuangtai'){
					form.value.fangwuzhuangtai = row[x];
					disabledForm.value.fangwuzhuangtai = true;
					continue;
				}
				if(x=='longitude'){
					form.value.longitude = row[x];
					disabledForm.value.longitude = true;
					continue;
				}
				if(x=='latitude'){
					form.value.latitude = row[x];
					disabledForm.value.latitude = true;
					continue;
				}
				if(x=='fulladdress'){
					form.value.fulladdress = row[x];
					disabledForm.value.fulladdress = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			form.value.fangwuzhuangtai=''
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('fangdongzhanghao') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.fangdongzhanghao = json.fangdongzhanghao
				disabledForm.value.fangdongzhanghao = true;
			}
			if(json.hasOwnProperty('fangdongxingming') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.fangdongxingming = json.fangdongxingming
				disabledForm.value.fangdongxingming = true;
			}
		})
		fangwuzhuangtaiLists.value = ",".split(',')
	}
	//
	//
	const backClick = () => {
		history.back()
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save=()=>{
		if(form.value.fangwutupian!=null) {
			form.value.fangwutupian = form.value.fangwutupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(``,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 
	.add_form{
		border: 1px solid #eee;
		border-radius: 0px;
		padding: 40px 7% 50px;
		background: url() no-repeat center bottom / 100% auto,#fcfcfc;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ff879850;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				// 
				.list_sel {
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ff879850;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
				// 
				.list_editor {
					border-radius: 0;
					padding: 0;
					margin: 0;
					background: #fff;
					width: 99%;
					min-height: 250px;
					border-color: #ddd;
					border-width: 1px;
					border-style: solid;
					height: auto;
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
						margin: 7px 0 0;
						color: #999;
						display: flex;
						font-size: 14px;
						justify-content: flex-start;
						align-items: center;
					}
					//
					.el-upload--picture-card {
						border: 1px solid #ff879850;
						cursor: pointer;
						border-radius: 20px;
						background: #fff;
						width: 150px;
						line-height: 90px;
						text-align: center;
						height: 80px;
						//
						.el-icon{
							color: #ff8798;
							font-weight: 600;
							font-size: 24px;
						}
					}
					.el-upload-list__item {
						border: 1px solid #ff879850;
						cursor: pointer;
						border-radius: 20px;
						background: #fff;
						width: 150px;
						line-height: 90px;
						text-align: center;
						height: 80px;
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		padding: 0 150px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
		.formModel_cancel {
			border: 0px solid #ccc;
			cursor: pointer;
			border-radius: 20px;
			padding: 0 40px;
			margin: 0 20px 0 0;
			color: #666;
			background: #ff879850;
			width: auto;
			font-size: 14px;
			line-height: 40px;
			transition: all 0.3s;
			height: 40px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 0px solid #11396190;
			cursor: pointer;
			border-radius: 20px;
			padding: 0 40px;
			margin: 0 20px 0 0;
			color: #fff;
