import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import fangdongList from '@/views/pages/fangdong/list'
import fangdongDetail from '@/views/pages/fangdong/formModel'
import fangdongAdd from '@/views/pages/fangdong/formAdd'
import fangwuxinxiList from '@/views/pages/fangwuxinxi/list'
import fangwuxinxiDetail from '@/views/pages/fangwuxinxi/formModel'
import fangwuxinxiAdd from '@/views/pages/fangwuxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import yuyuexinxiList from '@/views/pages/yuyuexinxi/list'
import yuyuexinxiDetail from '@/views/pages/yuyuexinxi/formModel'
import yuyuexinxiAdd from '@/views/pages/yuyuexinxi/formAdd'
import fangwuzulinList from '@/views/pages/fangwuzulin/list'
import fangwuzulinDetail from '@/views/pages/fangwuzulin/formModel'
import fangwuzulinAdd from '@/views/pages/fangwuzulin/formAdd'
import fangwuweixiuList from '@/views/pages/fangwuweixiu/list'
import fangwuweixiuDetail from '@/views/pages/fangwuweixiu/formModel'
import fangwuweixiuAdd from '@/views/pages/fangwuweixiu/formAdd'
import shenqingtuizuList from '@/views/pages/shenqingtuizu/list'
import shenqingtuizuDetail from '@/views/pages/shenqingtuizu/formModel'
import shenqingtuizuAdd from '@/views/pages/shenqingtuizu/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'fangdongList',
			component: fangdongList
		}, {
			path: 'fangdongDetail',
			component: fangdongDetail
		}, {
			path: 'fangdongAdd',
			component: fangdongAdd
		}
		, {
			path: 'fangwuxinxiList',
			component: fangwuxinxiList
		}, {
			path: 'fangwuxinxiDetail',
			component: fangwuxinxiDetail
		}, {
			path: 'fangwuxinxiAdd',
			component: fangwuxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'yuyuexinxiList',
			component: yuyuexinxiList
		}, {
			path: 'yuyuexinxiDetail',
			component: yuyuexinxiDetail
		}, {
			path: 'yuyuexinxiAdd',
			component: yuyuexinxiAdd
		}
		, {
			path: 'fangwuzulinList',
			component: fangwuzulinList
		}, {
			path: 'fangwuzulinDetail',
			component: fangwuzulinDetail
		}, {
			path: 'fangwuzulinAdd',
			component: fangwuzulinAdd
		}
		, {
			path: 'fangwuweixiuList',
			component: fangwuweixiuList
		}, {
			path: 'fangwuweixiuDetail',
			component: fangwuweixiuDetail
		}, {
			path: 'fangwuweixiuAdd',
			component: fangwuweixiuAdd
		}
		, {
			path: 'shenqingtuizuList',
			component: shenqingtuizuList
		}, {
			path: 'shenqingtuizuDetail',
			component: shenqingtuizuDetail
		}, {
			path: 'shenqingtuizuAdd',
			component: shenqingtuizuAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
